@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@500&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Lato", sans-serif;
  /* background-color: rgba(248, 246, 246, 0.8); */
  background-color: white;
  max-width: 1440px;
  margin: 0 auto;
  background: url(/covid-tracker/static/media/covid-scaled.bca757de.jpg);
}

/* HEADER COMPONENT */
.header {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.logo {
  max-width: 60px;
  margin: 1em 0;
}

.header__title {
  display: flex;
  width: 100%;
  justify-content: center;
  text-transform: uppercase;
  margin: 0 0 1.5em 0;
  font-family: "Comfortaa", cursive;
}

.header__dropdown {
  width: 300px;
  background-color: white;
}

.header__inputlabel {
  background: white;
}
.dropdown-div {
  display: flex;
  justify-content: center;
}

/* CONTAINERS */

.app__container {
  display: flex;
  margin-top: 2.5em;
}

.app__left {
  display: flex;
  flex-direction: column;
  flex: 0.9 1;
}
.app__right {
  display: flex;
  flex-direction: column;
  flex: 0.3 1;
}

/* STATS COMPONENT */

.stats__container {
  display: flex;
  justify-content: space-around;
  padding: 0 1.5em;
}

.stats {
  display: flex;
  margin: 3em 0;
  justify-content: space-between;
  transition: all 400ms ease-in-out;
  border-radius: 6px;
}
.stats:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  cursor: pointer;
}

