.card {
  padding: 0.5em;
  min-width: 300px;
  min-height: 210px;
  display: flex;
  flex-direction: column;
}

.card_content {
  display: flex;
  flex-direction: column;
}
.card__title {
  margin: 1em 0;
}

.card__today {
  margin: 1em 0;
  color: #e60e35;
}
.card--selected {
  border: 1px solid rgb(119, 9, 60);
  border-top: 20px solid rgb(139, 11, 71);
}
.card--green {
  color: rgb(23, 197, 23);
}

.card__total {
  margin: 1em 0;
  color: rgb(87, 84, 84);
  font-weight: 600;
  background-color: rgba(228, 226, 226, 0.5);
}

.card__label {
  font-size: 15px;
  margin: 0 0.5em;
  text-transform: uppercase;
}

/* SIDEBAR COMPONENT */

.sidebar-div {
  margin: 1em 0;
}
.sidebar {
  display: flex;
  width: 100%;
  max-height: 330px;
  margin: 1em 0;
  overflow: scroll;
}

.active {
  display: flex;
  flex-direction: column;
  width: 100%;
  font-size: 23px;
}

.active__title {
  margin: 0;
  color: rgb(143, 11, 92);
  background-color: white;
  width: 100%;
  border-bottom: 2px solid rgb(143, 11, 92);
  padding: 0.5em 0.25em;
  font-size: 20px;
}

.active__items {
  display: flex;
  justify-content: space-between;
  padding: 0.25em 1em;
}
.active__items:nth-of-type(odd) {
  background-color: rgba(236, 235, 233, 0.6);
}

.data-grid {
  width: 100%;
}

