/* MEDIA QUERIES */
@media (max-width: 1450px) {
  .app__container {
    margin: 0 2em;
  }
}

@media (max-width: 1200px) {
  body {
    margin: 2em;

    background: url(/covid-tracker/static/media/covid-mobile.79050094.jpg) center no-repeat;
    background-size: cover;
  }

  .stats__container {
    justify-content: space-around;
  }

  .stats {
    margin: 3em 0.5em;
  }

  .stats:hover {
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
  }

  .app__container {
    flex-direction: column;
    padding: 0.5em 0.25em;
    margin: 0 2em;
  }
}

@media (max-width: 860px) {
  body {
    background: url(/covid-tracker/static/media/bg1.0c339227.jpg) top no-repeat;
    background-size: cover;
  }
  .app__container {
    margin: 0 0.5em;
  }
  .stats__container {
    flex-direction: column;
    align-items: center;
    margin-bottom: 4em;
  }
  .stats {
    margin: 1em;
    max-width: 500px;
  }
  .active__title {
    padding: 0.5em;
    border-radius: 0.25em;
    border-bottom: 5px solid rgb(143, 11, 92);
    background-color: rgba(245, 245, 245, 0.8);
  }
  .sidebar {
    border-bottom: 5px solid rgb(143, 11, 92);
  }
}

footer {
  color: rgb(63, 62, 62);
  display: flex;
  flex-direction: column;
  margin: 2em 0 1em 2em;
}
footer span {
  color: #e64562;
}

@media (max-width: 390px) {
  body {
    font-size: smaller;
    background: url(/covid-tracker/static/media/bg1.0c339227.jpg) top no-repeat;
    background-size: cover;
  }
  .active__title {
    padding: 0.5em;
    border-radius: 0.25em;
    border-bottom: 5px solid rgb(143, 11, 92);
    background-color: rgba(245, 245, 245, 0.7);
  }
}


