.map__container {
  height: 400px;
  margin: 4em 2em;
  border: 25px solid rgb(231, 229, 229);
  border-radius: 2em;
}

@media (max-width: 390px) {
  .map__container {
    border: 10px solid rgba(143, 11, 92, 0.2) !important;
    border-radius: 2em;
  }
}

.popup-container {
  width: 160px;
  height: auto;
}

.popup-flag {
  height: 100px;
  width: 100%;
  background-size: cover;
  border-radius: 8px;
  border: 1px solid rgb(233, 232, 232);
}
.popup-flag img {
  width: 100px;
  border-radius: 5px;
}

.popup-name {
  font-size: 15px;
  font-weight: bold;
  margin-top: 6px;
}

.popup-data {
  font-size: 12px;
  margin-top: 4px;
}

@media (max-width: 390px) {
  .map__container {
    height: 400px;
    margin: 2em 0.25em;
    border: 15px solid rgb(231, 229, 229);
  }
}
